import { Fragment, useState } from "react";

import classes from "./Menu.module.css";

const Borrowing = () => {
  const [dropdown, setDropdown] = useState(false);

  const showDropDownHandler = () => {
    setDropdown(true);
  };
  const hideDropDownHandler = () => {
    setDropdown(false);
  };
  return (
    <Fragment>
      <li
        className={classes.menulist}
        onMouseEnter={showDropDownHandler}
        onMouseLeave={hideDropDownHandler}
      >
        <div className={classes.topnav}>Borrowing</div>
        {dropdown && (
          <div className={classes.dropdown}>
            <div className={classes["subnav-container"]}>
              <p className={classes["subnav-title"]}>Credit cards</p>
              <div className={classes.subnav}>
                <a href="/balance-transfer-credit-cards">
                  Balance transfer credit cards
                </a>
                <a href="/purchase-cards">Purchase cards</a>
                <a href="/rewards-cards">Reward cards</a>
                <a href="/credit-building-cards">Credit building cards</a>
                <a href="/premier-cards">Premier cards</a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>Loans</p>
              <div className={classes.subnav}>
                <a href="/personal-loan">Personal loan</a>
                <a href="/premier-loan">Premier loan</a>
                <a href="/car-loan">Car loan</a>
                <a href="/home-improvement-loan">Home Improvement loan</a>
                <a href="/top-up-your-loan">Top up your loan</a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>Mortgages</p>
              <div className={classes.subnav}>
                <a href="/switch-your-mortgage-rate">
                  Switch your mortgage rate
                </a>
                <a href="/remortgage-to-providence">Remortgage to providence</a>
                <a href="/first-time-buyers">First time buyers</a>
                <a href="/get-a-decision-in-principle">
                  Get a decision in principle
                </a>
                <a href="/mortgage-rates">Mortgage rates</a>
              </div>
            </div>
          </div>
        )}
      </li>
    </Fragment>
  );
};

export default Borrowing;
