export const updateSettings = async (data, type) => {
  let url;
  if (type === "pin") {
    // url = "http://127.0.0.1:5001/api/v1/users/updateMyPin";
    url = "/api/v1/users/updateMyPin";
  } else if (type === "data") {
    // url = "http://127.0.0.1:5001/api/v1/users/updateMe";
    url = "/api/v1/users/updateMe";
  }
  try {
    const res = await axios({
      method: "PATCH",
      url,
      data,
      // headers: {
      //   authorization: `Bearer ${jwt}`,
      // },
    });
    return res.data;
  } catch (err) {
    return err.response.data;
  }
};

