import { Fragment, useState } from "react";

import classes from "./Menu.module.css";

const Help = () => {
  const [dropdown, setDropdown] = useState(false);

  const showDropDownHandler = () => {
    setDropdown(true);
  };
  const hideDropDownHandler = () => {
    setDropdown(false);
  };
  return (
    <Fragment>
      <li
        className={classes.menulist}
        onMouseEnter={showDropDownHandler}
        onMouseLeave={hideDropDownHandler}
      >
        <div className={classes.topnav}>Help</div>
        {dropdown && (
          <div className={classes.dropdown}>
            <div className={classes["subnav-container"]}>
              <p className={classes["subnav-title"]}>Managing your account</p>
              <div className={classes.subnav}>
                <a href="/rising-cost-of-living">Rising cost of living</a>
                <a href="/ways-to-bank">Ways to bank</a>
                <a href="/card-support">Card support</a>
                <a href="/step-up-or-cancel-a-direct-debit">
                  Step up or cancel a direct debit
                </a>
                <a href="/step-up-or-cancel-a-standing-order">
                  Step up or cancel a standing order
                </a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>Fraud and security</p>
              <div className={classes.subnav}>
                <a href="/fraud-guide">Fraud guide</a>
                <a href="/received-a-text">Received a text?</a>
                <a href="/providence-safeguard">Providence Safeguard</a>
                <a href="/secure-key">Secure key</a>
                <a href="/our-digital-security-promise">
                  Our digital security promise
                </a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>Digital banking</p>
              <div className={classes.subnav}>
                <a href="/mobile-banking">Mobile banking</a>
                <a href="/online-banking">Online banking</a>
                <a href="/banking-at-home">Banking at home</a>
                <a href="/mobile-cheque-deposit">Mobile cheque deposit</a>
                <a href="/mobile-money-management">Mobile money management</a>
              </div>
            </div>
          </div>
        )}
      </li>
    </Fragment>
  );
};

export default Help;
