import { useDispatch } from "react-redux";
import { Link, useNavigate } from "react-router-dom";
import { useCookies } from "react-cookie";

import classes from "./Header.module.css";
import logo from "../../images/logo.png";
import Tab from "../admin-menus/Tab";
import { logout } from "../../api/api";
import { authActions } from "../../store.js/auth-slice";

const Header = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const removeCookie = useCookies(["jwt"])[1];

  const gotoHomepageHandler = () => {
    navigate("/");
  };

  const logoutHandler = () => {
    logout();
    removeCookie("jwt", { path: "/" });
    dispatch(authActions.logOut());
  };

  return (
    <div className={classes.header}>
      <div className={classes.logo} onClick={gotoHomepageHandler}>
        <img src={logo} alt="logo" />
      </div>
      <ul>
        <Tab title="Accounts" link="/account/admin/users" />
        <Tab title="Create account" link="/account/admin/new-account" />
      </ul>
      <Link to="/login" className={classes.logout} onClick={logoutHandler}>
        Logout
      </Link>
    </div>
  );
};

export default Header;
