import { Fragment, useState } from "react";

import classes from "./Menu.module.css";

const Insurance = () => {
  const [dropdown, setDropdown] = useState(false);

  const showDropDownHandler = () => {
    setDropdown(true);
  };
  const hideDropDownHandler = () => {
    setDropdown(false);
  };
  return (
    <Fragment>
      <li
        className={classes.menulist}
        onMouseEnter={showDropDownHandler}
        onMouseLeave={hideDropDownHandler}
      >
        <div className={classes.topnav}>Insurance</div>
        {dropdown && (
          <div className={classes.dropdown}>
            <div className={classes["subnav-container"]}>
              <p className={classes["subnav-title"]}>Insurance</p>
              <div className={classes.subnav}>
                <a href="/select-and-cover">Select and cover</a>
                <a href="/home-insurance">Home Insurance</a>
                <a href="/contents-insurance">Contents Insurance</a>
                <a href="/travel-insurance">Travel Insurance</a>
                <a href="/small-business-insurance">Small business Insurance</a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>Claims</p>
              <div className={classes.subnav}>
                <a href="/select-and-cover-claims">Select and cover claims</a>
                <a href="/home-insurance-claims">Home Insurance claims</a>
                <a href="/life-insurance-claims">Life Insurance claims</a>
                <a href="/travel-insurance-claims">Travel Insurance claims</a>
                <a href="/aspects-insurance-claims">Aspects Insurance claims</a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>Life</p>
              <div className={classes.subnav}>
                <a href="/life-cover">Life cover</a>
                <a href="/critical-illness-cover">Critical Illness cover</a>
                <a href="/income-protection-insurance">
                  Income protection Insurance
                </a>
                <a href="/protection-advice">Protection advice</a>
              </div>
            </div>
          </div>
        )}
      </li>
    </Fragment>
  );
};

export default Insurance;
