import { Fragment, useState } from "react";
import { Link } from "react-router-dom";

import classes from "./Menu.module.css";

const BankingMenu = () => {
  const [dropdown, setDropdown] = useState(false);

  const showDropDownHandler = () => {
    setDropdown(true);
  };
  const hideDropDownHandler = () => {
    setDropdown(false);
  };
  return (
    <Fragment>
      <li
        className={classes.menulist}
        onMouseEnter={showDropDownHandler}
        onMouseLeave={hideDropDownHandler}
      >
        <div className={classes.topnav}>Banking</div>
        {dropdown && (
          <div className={classes.dropdown}>
            <div>
              <p className={classes["subnav-title"]}>Current accounts</p>
              <div className={classes.subnav}>
                <Link to="/premier-account">Premier Account</Link>
                <a href="/advance-account">Advance Account</a>
                <a href="/bank-account">Bank Account</a>
                <a href="/business-account">Business Account</a>
                <a href="/childrens-account">Children's Account</a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>Savings account</p>
              <div className={classes.subnav}>
                <a href="/online-bonus-saver">Online Bonus Saver</a>
                <a href="/regular-savings-account">Regular Savings Account</a>
                <a href="/easy-access-savings-account">
                  Easy Access Savings Account
                </a>
                <a href="/fixed-rate-savings-account">
                  Fixed Rate Savings Account
                </a>
                <a href="/childrens-savings-account">
                  Children's Savings Account
                </a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>International services</p>
              <div className={classes.subnav}>
                <a href="/global-money-account">Global Money Account</a>
                <a href="/travel-money">Travel Money</a>
                <a href="/international-payments">International Payments</a>
                <a href="/currency-account">Currency Account</a>
                <a href="/international-ban-account">
                  International Bank Account
                </a>
              </div>
            </div>
          </div>
        )}
      </li>
    </Fragment>
  );
};

export default BankingMenu;
