import { Fragment } from "react";
import {
  Navigate,
  Route,
  RouterProvider,
  createBrowserRouter,
  createRoutesFromElements,
} from "react-router-dom";
import { useSelector } from "react-redux";
import Transition from "react-transition-group/Transition";

import Login from "./pages/Login";
import MyAccount from "./pages/MyAccount";
import Security from "./pages/Security";

import Static from "./pages/Static";
import TransHistory from "./pages/TransHistory";
import AlertModal from "./components/UI/AlertModal";
import Premier from "./components/static/Premier";

import MainBody from "./components/admin/MainBody.js";
import UserDetails from "./components/admin/UserDetails";
import Create from "./components/admin/Create";
import Transactions from "./components/admin/Transactions";
import RootLayout from "./pages/RootLayout";
import AdminLayout from "./pages/AdminLayout";
import { loader as transLoader } from "./components/admin/Transactions";
import { loader as usersLoader } from "./components/admin/MainBody.js";
import { loader as dashboardLoader } from "./pages/MyAccount";
import { loader as historyLoader } from "./pages/TransHistory";
import Error from "./pages/Error";
import Contact from "./pages/Contact";
import ForgotPinPage from "./pages/ForgotPinPage";
import ResetPinPage from "./pages/ResetPinPage";

const router = createBrowserRouter(
  createRoutesFromElements(
    <Route path="/" element={<RootLayout />} errorElement={<Error />}>
      <Route index element={<Static />} />
      <Route path="/premier-account" element={<Premier />} />
      <Route path="/login" element={<Login />} />
      <Route path="/forgot-pin" element={<ForgotPinPage />} />
      <Route path="/reset-pin/:token" element={<ResetPinPage />} />
      <Route path="/contact-us" element={<Contact />} />
      <Route path="/login/security" element={<Security />} />
      <Route path="/account" element={<MyAccount />} loader={dashboardLoader} />
      <Route
        path="/account/dashboard"
        element={<MyAccount />}
        loader={dashboardLoader}
      />
      <Route
        path="/account/transaction-history"
        element={<TransHistory />}
        loader={historyLoader}
      />
      <Route path="/account/admin/*" element={<AdminLayout />}>
        <Route path="users" element={<MainBody />} loader={usersLoader} />
        <Route path="users/:userId" element={<UserDetails />} />
        <Route path="new-account" element={<Create />} />
        <Route
          path="users/:userId/transactions"
          element={<Transactions />}
          loader={transLoader}
        />
      </Route>
      <Route path="*" element={<Navigate to="/" />} />
    </Route>
  )
);

const App = () => {
  const showModal = useSelector((state) => state.alertSlice.showModal);

  return (
    <Fragment>
      <RouterProvider router={router} />
      <Transition mountOnEnter unmountOnExit in={showModal} timeout={1000}>
        {(state) => <AlertModal />}
      </Transition>
    </Fragment>
  );
};

export default App;
