import { Fragment, useState } from "react";

import classes from "./Menu.module.css";

const WellBeing = () => {
  const [dropdown, setDropdown] = useState(false);

  const showDropDownHandler = () => {
    setDropdown(true);
  };
  const hideDropDownHandler = () => {
    setDropdown(false);
  };
  return (
    <Fragment>
      <li
        className={classes.menulist}
        onMouseEnter={showDropDownHandler}
        onMouseLeave={hideDropDownHandler}
      >
        <div className={classes.topnav}>Wellbeing</div>
        {dropdown && (
          <div className={classes.dropdown}>
            <div className={classes["subnav-container"]}>
              <p className={classes["subnav-title"]}>Financial fitness</p>
              <div className={classes.subnav}>
                <a href="/everyday-budgetimg">Everyday budgeting</a>
                <a href="/managing-debts">Managing debts</a>
                <a href="/growing-your-money">Growing your money</a>
                <a href="/staying-safe-and-secure">Staying safe and secure</a>
                <a href="/learning-about-money">Learning about money</a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>New beginnings</p>
              <div className={classes.subnav}>
                <a href="/expending-your-family">Expanding your family</a>
                <a href="/getting-married">Getting married</a>
                <a href="/planning-for-retirement">Planning for retirement</a>
                <a href="/buying-your-first-home">Buying your first home</a>
                <a href="/moving-abroad">Moving abroad</a>
              </div>
            </div>
            <div>
              <p className={classes["subnav-title"]}>Life events</p>
              <div className={classes.subnav}>
                <a href="/bereavement-support">Bereavement support</a>
                <a href="/assisting-someone-with-their-money">
                  Assisting someone with their money
                </a>
                <a href="/separation">Separation</a>
                <a href="/losing-your-job">Losing your job</a>
                <a href="/power-of-attorney">Power of Attorney</a>
              </div>
            </div>
          </div>
        )}
      </li>
    </Fragment>
  );
};

export default WellBeing;
