import { Link } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";

import classes from "./Card.module.css";
import { transferActions } from "../../store.js/transfer-slice";
import { alertActions } from "../../store.js/alert-slice";
import { deactiveAccount } from "../../api/api";
import { deleteAccount } from "../../api/api";
import LoadSpinner from "../UI/LoadSpinner";

const Card = (props) => {
  const dispatch = useDispatch();
  const showSpinner = useSelector((state) => state.transferSlice.showSpinner);

  const { user } = props;

  const deleteHandler = async () => {
    dispatch(transferActions.showSpinner());

    const data = await deleteAccount(user._id);

    if (data === "") {
      dispatch(transferActions.hideSpinner());
      dispatch(
        alertActions.setState({
          message: "Account deleted successfully",
          status: "success",
        })
      );
    } else {
      dispatch(transferActions.hideSpinner());
      dispatch(
        alertActions.setState({ message: data.message, status: "error" })
      );
    }
  };

  const deactivateHandler = async () => {
    dispatch(transferActions.showSpinner());

    const data = await deactiveAccount("blocked", user._id);

    if (data.status === "success") {
      dispatch(transferActions.hideSpinner());
      dispatch(
        alertActions.setState({ message: data.message, status: data.status })
      );
    } else {
      dispatch(transferActions.hideSpinner());
      dispatch(
        alertActions.setState({ message: data.message, status: "error" })
      );
    }
  };

  return (
    <li>
      <div className={classes.card}>
        {showSpinner && <LoadSpinner />}
        <div className={classes.actions}>
          <Link to={`/account/admin/users/${user._id}`}>Update Profile</Link>
          <Link to={`/account/admin/users/${user._id}/transactions`}>
            Update Transactions
          </Link>
        </div>
        <p>{user.name}</p>
        <p>{user.role}</p>
        <p>{user.status}</p>
        <div className={classes.photo}>
          <img src={user.photo} alt="user-name" />
        </div>
        <div className={classes.actions}>
          <button type="button" onClick={deactivateHandler}>
            Block account
          </button>
          <button type="button" onClick={deleteHandler}>
            Delete Account
          </button>
        </div>
      </div>
    </li>
  );
};

export default Card;
